import request from '../../utils/request.js';
import {
	mapState,
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			checked: [],
			totalPrice: 0,
			scheme: []
		}
	},
	computed: {
		...mapState(['shapecode', 'jsId'])
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
		});
		request("/api/auth/tabLists", "POST", {}).then(res => {
			console.log(res.data.data);
			res.data.data.forEach(item => {
				item.num = 0;
				item.totalPrice = 0;
				item.checked = false;
			});
			this.scheme = res.data.data
			uni.hideLoading();
		});
		request('/api/auth/getOrder', 'POST', {
			bar_id: this.shapecode
		}).then(res => {
			this.upJSId(res.data[0].consignor_id);
			console.log(res);
		});
	},
	methods: {
		...mapMutations(['upJSId']),
		add(e) {
			const idx = e.currentTarget.dataset.index;
			const arr = [];
			this.scheme[idx].num += 1;
			if (idx === 0) {
				this.scheme[idx].num = 1;
			}
			if (idx === 2) {
				this.scheme[idx].num = 1;
			}
			this.scheme[idx].checked = true;
			this.scheme[idx].totalPrice = this.scheme[idx].num * this.scheme[idx].price;
			this.totalPrice = 0;
			this.scheme.forEach(item => {
				this.totalPrice += item.totalPrice;
				if (item.checked) {
					arr.push(item);
				}
			});
			this.checked = arr;
		},
		reduce(e) {
			const idx = e.currentTarget.dataset.index;
			const arr = [];
			if (this.scheme[idx].num <= 1 && this.scheme[idx].checked !== false) {
				this.scheme[idx].num = 0;
				this.scheme[idx].totalPrice = this.scheme[idx].num * this.scheme[idx].price;
				this.totalPrice -= this.scheme[idx].price;
				this.scheme[idx].checked = false;
				this.scheme.forEach(item => {
					if (item.checked) {
						arr.push(item);
					}
				});
				this.checked = arr;
				console.log(this.checked, idx);
				return;
			}
			if (this.scheme[idx].num <= 1) {
				return;
			}
			this.scheme[idx].num -= 1;
			this.scheme[idx].totalPrice = this.scheme[idx].num * this.scheme[idx].price;
			this.totalPrice -= this.scheme[idx].price;
		},

		setSubmit() {
			if (this.checked.length <= 0) {
				uni.showToast({
					title: '至少选择一个方案',
					icon: 'none'
				});
				return;
			}
			uni.showLoading({
				title: '加载中...',
			});
			let scheme = "";
			let tab_ids = "";
			this.scheme.forEach(item => {
				if (item.num !== 0) {
					scheme += `${item.title} ￥${item.price}*${item.num} `;
					tab_ids += `,${item.id}`;
				}
			});
			tab_ids += ",";
			request("/api/auth/scheme", "POST", {
				scheme,
				tab_ids,
				bar_id: this.shapecode,
				user_id: this.jsId,
				price: this.totalPrice
			}).then(res => {
				console.log(res);
				uni.hideLoading();
				uni.showToast({
					title: '设置成功',
					icon: 'none',
					duration: 2000,
					complete: () => {
						setTimeout(() => {
							uni.navigateTo({
								url: '../sweep/sweep',
							});
						}, 1000);
					}
				});
			}).catch(error => {
				uni.hideLoading();
				uni.showToast({
					title: '设置失败',
					icon: 'none'
				});
			});
		}
	}
}
